.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.App-section {
  height: 50vh;
  overflow-y: hidden;
  border: 1px solid #09d3ac;
  display: flex;
  justify-content: center;
  align-items: center;
}
